import {Button, Col, Form, Modal, Row} from "react-bootstrap";
import React, {useEffect, useState} from 'react';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {
  faDownload,
  faExclamationCircle,
  faTimesCircle,
  faUpload
} from "@fortawesome/free-solid-svg-icons";
import {K} from "../../Constants";
import {apiCambioStato, apiDeleteDocumento, apiDownloadDocumento, apiNota, apiUploadDocumento} from "../../http/api";
import MyImage from '../../img/image_25.png'
import {getStatoDesc, getStatoEnum} from "../../http/helpers";
import ReactDOM from "react-dom";

export const SezioneDocumento = ({
                                   tipoDocumento, classeDocumentoId,
                                   statoDocumento, listaDoc, notaFromDoc, isProspect, update
                                 }) => {
  const [stato, setStato] = useState(statoDocumento)
  const [file, setFile] = useState()
  const [fileSelected, setFileSelected] = useState()
  const [showModal, setShowModal] = useState(false);
  const [showModalRifiuto, setShowModalRifiuto] = useState(false);
  const [showModalDoc, setShowModalDoc] = useState({show: false, id: undefined});
  const [nota, setNota] = useState({nota: null})

  const handleClickOutside = event => {
    const domNode = ReactDOM.findDOMNode(this);

    if (!domNode || !domNode.contains(event.target)) {
      setFileSelected(undefined)
    }
  }

  useEffect(() => {
    document.addEventListener('click', handleClickOutside, true);
    return () => {
      document.removeEventListener('click', handleClickOutside, true);
    };
  }, []);

  const upload = () => {
    apiUploadDocumento(K.serviceUrl, classeDocumentoId)
      .then(() => update());
  };

  const deleteDoc = (docId) => {
    apiDeleteDocumento(K.serviceUrl, docId)
      .then(() => update());
  };

  // const updateStato = (stato) => {
  //   setStato(stato)
  // };

  const selectFile = (file, fileIndex) => {
    setFile(file);
    setFileSelected(fileIndex)
  };

  const handleChange = event => {
    if (event.target.value === 'RIFIUTATO') {
      setShowModal(true);
    }
    apiCambioStato(K.serviceUrl, {idClasseDocumento: classeDocumentoId, stato: event.target.value})
      .then(() => {
        setStato(event.target.value)
        console.log(stato)
        update();

      })
  };

  const invia = () => {
    apiNota(K.serviceUrl, classeDocumentoId, nota).then(() => {
      console.log('ok nota')
      update();
    }).catch(reason => console.log(reason))
  }

  const handleNota = (e) => {
    setNota(e.target.value);
  }


  const download = (id) => {
    return apiDownloadDocumento(K.serviceUrl, id);
  };
  return (<>

      <Modal show={showModal} onHide={() => setShowModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Non Accettare</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group
              className="mb-3"
              controlId="message">
              <Form.Label>Inserisci motivo del rifiuto da notificare al cliente specificando i documenti da
                ricaricare</Form.Label>
              <Form.Control as="textarea" rows={3} onChange={(event) => handleNota(event)}/>
            </Form.Group>
          </Form>
        </Modal.Body>

        <Modal.Footer>
          <Button variant="primary" onClick={() => {
            invia();
            setShowModal(false);
          }}>Invia</Button>
        </Modal.Footer>
      </Modal>


      <Modal show={showModalRifiuto} onHide={() => setShowModalRifiuto(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Documentazione Rifiutata</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>{notaFromDoc}</p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="primary" onClick={() => {
            setShowModalRifiuto(false);
          }}>Ok</Button>
        </Modal.Footer>
      </Modal>


      <Modal show={showModalDoc.show} onHide={() => setShowModalDoc({show: false, id: undefined})}>
        <Modal.Header closeButton>
          <Modal.Title>Cancella</Modal.Title>
        </Modal.Header>

        <Modal.Body>
          <p>Sei sicuro di voler eliminare il documento?
          </p>
        </Modal.Body>

        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowModalDoc({show: false, id: undefined})}>No</Button>
          <Button variant="primary" onClick={() => {
            deleteDoc(showModalDoc.id);
            setShowModalDoc({show: false, id: undefined});
          }}>Si</Button>
        </Modal.Footer>

      </Modal>

      <Row className={'mt-2 p-2'}>
        <Col md={5}>
          <p className={'margin-custom-l document-text'}>{tipoDocumento}</p>
        </Col>
        {tipoDocumento !== "Altro" ?
          <>
            <Col md={3} className={'align-to-end '}>
              {listaDoc.map((value, index) => (
                <span key={value.id}
                      className={'p-1 me-1  border border-3 position-relative ' + ((fileSelected === index) ? 'rounded border-primary ' : 'border-white')}>
            <img src={MyImage} onClick={event => selectFile(value.id, index)}/>
                  {fileSelected === index && isProspect &&
                    <FontAwesomeIcon
                      style={{position: 'absolute', top: '-75%', left: '25%', color: '#0275d8', cursor: 'pointer'}}
                      icon={faTimesCircle} onClick={() => setShowModalDoc({show: true, id: value.id})}/>}
          </span>
              ))}
            </Col>
            {listaDoc.length > 0 ?
              <Col md={2} className={' d-flex justify-content-center '}>
                {isProspect ? <p
                    className={listaDoc.length > 0 ? statoDocumento : ''}>{listaDoc.length > 0 ? getStatoDesc(statoDocumento) : ''}</p> :
                  <select className={'form-select-xs ' + statoDocumento + '-select'} aria-label="Default select example"
                          defaultValue={statoDocumento} onChange={handleChange}>
                    <option value="APPROVATO">Approvato</option>
                    <option value="IN_LAVORAZIONE">In Lavorazione</option>
                    <option value="RIFIUTATO">Rifiutato</option>
                  </select>
                }
              </Col> :
              <Col md={2}>

              </Col>
            }
          </>
          :

          <Col md={5} className={'align-to-end'}>

            {listaDoc.map((value, index) => (
              <span key={value.id}
                    className={'p-1 me-1  border border-3 position-relative ' + ((fileSelected === index) ? 'rounded border-primary ' : 'border-white')}>
          <img src={MyImage} onClick={event => selectFile(value.id, index)}/>
                {fileSelected === index && isProspect &&
                  <FontAwesomeIcon
                    style={{position: 'absolute', top: '-75%', left: '25%', color: '#0275d8', cursor: 'pointer'}}
                    icon={faTimesCircle} onClick={() => deleteDoc(value.id)}/>}
          </span>))
            }
          </Col>
        }
        <Col md={2} className={'align-content-start'}>
          {isProspect ?
            <>
              <button className={'upload-buttons'} onClick={() => upload()}>
          <span className={'highlighted-span document-text secondary-color'}>Carica
            <FontAwesomeIcon icon={faUpload} className={'primary-color mx-1'}></FontAwesomeIcon></span>
              </button>
              {(notaFromDoc && statoDocumento !== 'APPROVATO') &&

                  <FontAwesomeIcon icon={faExclamationCircle} className={'primary-color fa-lg mx-2 pointer-icon'}
                                   onClick={(event) => setShowModalRifiuto(true)}></FontAwesomeIcon>
                }
            </>
            :
            <>
              <button className={'upload-buttons'} onClick={() => download(file)}>
          <span className={'highlighted-span document-text secondary-color'}>Scarica
            <FontAwesomeIcon icon={faDownload} className={'primary-color mx-1'}></FontAwesomeIcon></span>
              </button>
            </>
          }
        </Col>
        <hr/>
      </Row>
    </>
  )
}

